import React from 'react';
import styled from 'styled-components'
import { scroller, animateScroll as scroll } from 'react-scroll'


const FullHeightFlexContainer = ({ children, sectionIndex, displayScrollButton = true, displayBackToTop = false, id }) => {
    function scrollToSection() {
        scroller.scrollTo(`section-${sectionIndex + 1}`, {
            duration: 800,
            delay: 0,
            smooth: 'easeInOutQuart'
          })
    }

    return (
        <Container name={`section-${sectionIndex}`} id={id}>
            {children}
            {displayScrollButton &&
                <ScrollButton
                    onClick={scrollToSection}
                    aria-label={`Scroll to next section`}
                />
            }
            {displayBackToTop &&
                <ScrollButton
                    onClick={() => scroll.scrollToTop()}
                    aria-label={`Scroll to top`}
                    scrolltoTop={true}
                />
            }
        </Container>
    )
}

const Container = styled.div`
  min-height: 100vh;
  width: 100%;
  position: relative;
  display: flex;
  flex
  justify-content: center;
  align-items: center;
`;
const ScrollButton = styled.button`
    position: absolute;
    bottom: 0;
    left: calc(50% - 20px);
    display: block;
    width: 44px;
    height: 44px;
    background: url(data:image/svg+xml;base64,) no-repeat center transparent;
    background-size: 44px;
    cursor: pointer;
    ${props => props.scrolltoTop && `transform: rotate(180deg);`}
`

export default FullHeightFlexContainer;